	module.exports = {"data":{"post":{"html":"<p>NDRInfo, our regional news radio station, runs a Jewish culture program on Fridays called <a href=\"http://www.ndrinfo.de/ndrinfo_pages_std/0,2758,SPM690,00.html\">Schabat Schalom</a>. Last week (or was it the week before) they had a nice talk on bi- and multi-lingual puns. Plays on words always involve some sort of secret knowledge to get the joke, and if you have to explain yourself, the joke is lost. For example, I titled my last post on the Tower of Light tribute in NYC <em>Leuchtturm</em> (\"lighthouse\"), which breaks down into \"light\" and \"tower\". Only a non-native speaker of German would think of calling the tribute a <em>Leuchtturm</em>, and now that I've explained it, the humor is gone...</p>\n<p>Anyway, the rabbi last week explained how \"sabos\" were the course wooden shoes of early industrial workers in France, and when they would protest their poor working conditions by stuffing their shoes into the machines, they were committing \"sabotage\". In the same way, the sabbath forces our hectic way of life to come to a halt for at least one day a week, a form of \"Schabat-tage\". And if you choose to celebrate the sabbath by buying expensive Italian olive-oil bread, you then have \"Ciabat-tage\", which if you pronounce Italian poorly enough sounds nearly the same. </p>\n<p>A French-English-Hebrew-Italian pun, told in German. I was impressed.</p>","fields":{"title":"Schabat Schalom","date":"14 Sep 2004","url":"https://papascott.de/2004-09-14-schabat-schalom/"}}},"pathContext":{"slug":"/2004-09-14-schabat-schalom/","prev":{"fields":{"title":"Exact Change","date":"14 Sep 2004","slug":"/2004-09-14-exact-change/"}},"next":{"fields":{"title":"Spoon-Feed or Fork-Feed?","date":"14 Sep 2004","slug":"/2004-09-14-spoon-feed-or-fork-feed/"}}}}

